/*
 * 1. Correct the line height in all browsers.
 * 2. Prevent adjustments of font size after orientation changes in
 *    IE on Windows Phone and in iOS.
 */
html {
  line-height: 1.15;
  /* 1 */
  /* stylelint-disable plugin/no-unsupported-browser-features */
  -ms-text-size-adjust: 100%;
  /* 2 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
  /* stylelint-enable plugin/no-unsupported-browser-features */
}

/*
 * 1. Remove the margin in all browsers (opinionated).
 * 2. Font-smoothing declaration makes OS X fonts look more consistent
 *    with other systems.
 * 3. Set main font.
 */
body {
  margin: 0;
  /* 1 */
  -moz-osx-font-smoothing: grayscale;
  /* 2 */
  -webkit-font-smoothing: antialiased;
  /* 2 */
  font-family: "Helvetica Neue", helvetica, arial, "Lucida Grande", sans-serif;
  font-size: 16px;
  /* 3 */
  line-height: 1.5;
  /* 3 */
}

/*
 * Universal box sizing.
 */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/*
 * Remove gaps in links underline in iOS 8+ and Safari 8+.
 */
a {
  -webkit-text-decoration-skip: objects;
  /* 2 */
  text-decoration: none;
}

a:hover, a:focus {
  text-decoration: underline;
}

/*
 * Corrects some browsers defaulting to bolder.
 */
b,
strong {
  font-weight: 700;
}

/*
 * Show the overflow in IE / Edge.
 */
button,
input {
  overflow: visible;
}

/*
 * Style form elements.
 */
select {
  font-size: 13px;
  padding: 3px 6px;
  border: 1px solid #ccc;
  border-radius: 3px;
  outline: none;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.15);
}

/*
 * Style blockquote.
 */
blockquote {
  color: #666;
  font-size: 18px;
  font-style: italic;
  line-height: 1.7;
  overflow: hidden;
}

blockquote cite {
  display: block;
  font-style: normal;
  font-weight: 700;
  margin-top: 10px;
}

/*
 * Various normalizations.
 */
dfn,
cite,
em,
i {
  font-style: italic;
}

abbr,
acronym {
  border-bottom: 1px dotted #666;
  cursor: help;
}

mark,
ins {
  background: #eee;
  text-decoration: none;
}

small {
  font-size: 80%;
}

big {
  font-size: 125%;
}

/*
 * Correct the odd appearance in Chrome and Safari.
 */
/* stylelint-disable plugin/no-unsupported-browser-features */
[type='search'] {
  -webkit-appearance: textfield;
}

[type='search']::-webkit-search-cancel-button, [type='search']::-webkit-search-decoration {
  -webkit-appearance: none;
}

/* stylelint-enable plugin/no-unsupported-browser-features */
/*
 * Resets the button to consistent native appearance in all browsers.
 */
button {
  overflow: visible;
  border: 0;
  font: inherit;
  -webkit-font-smoothing: inherit;
  letter-spacing: inherit;
  background: none;
  cursor: pointer;
}

/*
 * Make images stay within the limits of their container.
 */
img {
  height: auto;
  max-width: 100%;
}

/*
 * Resetting margins and padding.
 */
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
figure,
address,
ol,
ul,
dl,
li,
dt,
dd,
button,
input,
optgroup,
select,
textarea,
fieldset,
legend {
  margin: 0;
  padding: 0;
}

fieldset {
  padding: 0;
  border: none;
}

legend {
  display: block;
}

iframe {
  border: none;
}

/*
 * Tables
 */
table {
  border: none;
  border-top: 1px solid #ccc;
  border-collapse: collapse;
  margin: 0 0 20px;
  width: 100%;
}

thead th {
  border-bottom: 2px solid #ccc;
  padding-bottom: 10px;
}

th {
  padding: 8px 0;
  text-align: left;
  border: none;
}

td {
  padding: 8px;
  border: none;
}

tr {
  border-bottom: 1px solid #ccc;
}

/*
 * Hidden element.
 */
.is-hidden,
.hidden {
  display: none !important;
}

/*
 * WPCom Globals
 */
img.latex {
  border: none;
  vertical-align: middle;
}

.video-player {
  border: 0;
  margin: auto;
  padding: 5px;
  text-align: center;
  max-width: 100%;
}

.screen-reader-text {
  position: absolute;
  left: -1000em;
}

.comment object,
.comment embed,
.embed-vimeo iframe,
.embed-youtube iframe {
  max-width: 100%;
}

.author-card {
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.author-card > .avatar {
  border-radius: 50%;
  height: 74px;
  overflow: hidden;
  margin: 0 auto;
  width: 75px;
}

.author-card > .avatar picture {
  display: block;
}

.author-card > .avatar .image {
  display: block;
}

.author-card > .content .meta {
  text-align: center;
}

.follow-link {
  background-color: #ebebeb;
  border-radius: 50px;
}

.follow-link:visited {
  color: #222222;
}

.author-card .follow-link {
  display: inline-block;
  margin-bottom: 10px;
  margin-top: 10px;
  padding: 4px 25px 4px 4px;
}

.author-card .follow-link .icon {
  margin-right: 19px;
}

.icon {
  display: inline-block;
  height: 25px;
  width: 25px;
}

.icon__facebook {
  background: #3a5795;
  border-radius: 50%;
  color: #ffffff;
}

.icon__facebook svg {
  height: 12px;
  width: 6px;
}
